let presets = {
    Shadow: {
        color: '#000000',
        size: 5,
        blur: 40
    },
    SimpleBorder: {
        color: '#ffff00',
        size: 5,
        blur: 5
    },
    LargeBlur: {
        color: '#ffff00',
        size: 10,
        blur: 20
    },
    Halo: {
        color: '#ffff00',
        size: 10,
        blur: 50
    },
    VeryLargeBlur: {
        color: '#ffff00',
        size: 10,
        blur: 100
    },
    Cartoon: {
        color: '#ffff00',
        size: 6,
        blur: 5
    }
};
class LightBorder {
    static apply(obj, params) {
        params = params || {};
        // create final preset since original preset surcharged with params argument.
        var preset = presets[params.preset || 'SimpleBorder'];
        for (var prop in params) {
            preset[prop] = params[prop];
        }
        //jx.monitor.setMarker("a")
        preset.rgbColor = LightBorder.hexToRGB(preset.color);
        //jx.monitor.setMarker("b")
        obj.shadow = new createjs.Shadow(preset.color, preset.size, preset.size, preset.blur);
        obj.cache(0, 0, obj.getBounds().width, obj.getBounds().height);
    }
    static cancel(obj) {
        obj.shadow = null;
        obj.uncache();
    }
    static hexToRGB(hexValue) {
        function cutHex(h) { return (h.charAt(0) == "#") ? h.substring(1, 7) : h; }
        return {
            r: parseInt((cutHex(hexValue)).substring(0, 2), 16),
            g: parseInt((cutHex(hexValue)).substring(2, 4), 16),
            b: parseInt((cutHex(hexValue)).substring(4, 6), 16)
        };
    }
    ;
}
export default LightBorder;
